<template>
    <div style="background-color: #f4f5f5;">
        <RawMaterialName :item="{}" :rawmaterialname="traceTitle"></RawMaterialName>
        <div class="mc-wrap mc-info flex-sb">
            <h3 style="font-weight: bold; flex-shrink: 0; width: 80px; margin-right: 15px">工业标识码</h3>
            <div class="currency-price">
                <span style="font-weight: bold; word-break: break-all;">{{ idisCode }}</span>
            </div>
        </div>
        <div>
            <div class="detail-card" v-for="(item, j) in traceDataList" :key="j">
                <h3 class="detail-title">{{ item.type }}</h3>
                <div class="mc-info mc-wrap">
                    <div class="trace-detail" v-for="(r, i) in item.detail" :key="i">
                        <div class="trace-detail-key">
                            {{ r.key }}
                        </div>
                        <span class="pull-right">{{ r.value }}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { getJsonResult } from '@/utils/server-wrapper'
import Badge from '@/assets/images/code-information-badge-3.png'
import RawMaterialName from '@/components/product-template/RawMaterialName'
export default {
    components: { RawMaterialName },
    data() {
        return {
            Badge,
            traceTitle: '',
            idisCode: '',
            traceDataList: [
                // {
                //     type: '生产情况',
                //     detail: [
                //         { key: '流水号', value: '008' },
                //         { key: '工序名称', value: 'CP001' }
                //     ]
                // },
                // {
                //     type: '原材料信息',
                //     detail: [
                //         { key: '材料名称', value: '铝材质' },
                //         { key: '检测编号', value: 'CP001' }
                //     ]
                // }
            ]
        }
    },
    async beforeRouteEnter(to, from, next) {
        const idisCode = to.query.code
        const materialName = to.query.materialName
        const info = await getJsonResult(`/wx/codeInformation?codeId=${encodeURIComponent(idisCode)}`)
        const codeAttr = info.codeAttr
        const resCodeAttrList = []
        codeAttr.forEach(item => {
            const catName = item.attrCategoryName
            if (catName && catName.substring(0, catName.indexOf('/')) === materialName) {
                resCodeAttrList.push(item)
            }
        })
        //进行去重处理
        const res = new Map()
        const resCodeAttr = resCodeAttrList.filter(
            resCodeAttrList =>
                !res.has(resCodeAttrList.attrCategoryName) && res.set(resCodeAttrList.attrCategoryName, 1)
        )
        // console.log(resCodeAttr)
        const attrCategoryNameList = []
        resCodeAttr.forEach(item => {
            attrCategoryNameList.push(item.attrCategoryName)
        })

        // console.log(attrCategoryNameList)
        const traceDataList = []
        attrCategoryNameList.forEach(k => {
            const list = []
            codeAttr.forEach(item => {
                const categoryName = item.attrCategoryName
                if (categoryName === k) {
                    let valueMap = {}
                    valueMap = { key: item.attrNameCn, value: item.attrValue }
                    list.push(valueMap)
                }
            })
            let map = { type: k.substring(k.lastIndexOf('/') + 1), detail: list }
            traceDataList.push(map)
        })
        // console.log(traceDataList)
        next(vm => {
            vm.$data.idisCode = idisCode
            vm.$data.traceTitle = materialName + '溯源信息'
            vm.$data.traceDataList = traceDataList
        })
    }
}
</script>
